.live {
  color: #fff;
  background-color: #000;
  padding: 6rem 0;
}

.live__title {
  text-align: center;
  color: #fff;
  letter-spacing: .08em;
  margin-bottom: 1em;
  font-family: Noto Sans JP, sans-serif;
  font-size: 36px;
}

.live__tabs {
  justify-content: center;
  gap: 1rem;
  margin-top: 1.5rem;
  display: flex;
}

.live__tab {
  color: #fff;
  cursor: pointer;
  opacity: .7;
  background: none;
  border: none;
  border-radius: 2.5rem;
  justify-content: center;
  align-items: center;
  width: 46px;
  height: 46px;
  margin: 0 20px;
  transition: opacity .15s ease-in-out, transform .15s ease-in-out;
  display: inline-flex;
}

.live__tab i {
  font-size: 50px;
  line-height: 1;
}

.live__tab:hover {
  opacity: .9;
  transform: translateY(-1px);
}

.live__tab.is-active {
  opacity: 1;
}

.live__tab:focus-visible {
  outline-offset: 2px;
  outline: 2px solid #fff;
}

.live__stage {
  aspect-ratio: 16 / 9;
  width: 100%;
  max-width: 960px;
  margin: 0 auto;
  position: relative;
}

.live .embed-16x9 {
  border-radius: .5rem;
  width: 100%;
  padding-top: 0;
  position: absolute;
  inset: 0;
  overflow: hidden;
}

.live .embed-16x9 iframe {
  border-radius: inherit;
  width: 100%;
  height: 100%;
  position: absolute;
  inset: 0;
}

.live__pane {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  width: 100%;
  height: 100%;
  transition: opacity .8s;
  position: absolute;
  inset: 0;
}

.live__pane.is-active {
  opacity: 1;
  visibility: visible;
  pointer-events: auto;
}

.live__placeholder {
  color: #bbb;
  text-align: center;
  background: #111;
  border: 2px dashed #444;
  border-radius: .75rem;
  width: 80%;
  min-width: 893px;
  max-width: 900px;
  margin: 0 auto;
  position: relative;
}

.live__placeholder:before {
  content: "";
  padding-top: 56.25%;
  display: block;
}

.live__placeholder p {
  margin: 0;
  padding: 1.5rem;
  font-size: 64px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@media (width >= 768px) {
  .live__placeholder p {
    font-size: 96px;
  }
}

