  transform: translateY(-4px);
  box-shadow: 0 4px 6px -1px #0000001a, 0 2px 4px -1px #0000000f;
}

.tooltip--visible {
  opacity: 1;
  transform: translateY(0);
}

.tooltip--deadline:after {
  content: "";
  border: 6px solid #0000;
  border-top-width: 0;
  border-bottom-color: #000000e6;
  position: absolute;
  bottom: -6px;
  left: 50%;
  transform: translateX(-50%);
}

